import "./styles.css";
import React, { useState } from "react";

export default function App() {
  const [text, setText] = useState("");

  return (
    <div className="App">
      <h1>Mardown preview</h1>
      <textarea
        name="text"
        id="text"
        rows="10"
        value={text}
        onChange={(e) => setText(e.target.value)}
        className="textarea"
      />
      <Preview markdown={text} />
    </div>
  );
}

function Preview({ markdown }) {
  return <div></div>;
}
